.text-center {
  text-align: center;
}

/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Poppins", sans-serif;
  font-size: 1.6rem;
}

p {
  color: #FFFFFF;
  font-size: 2rem;
  margin: 0px;
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

h1, h2, h3 {
  font-family: "Poppins", sans-serif;
  margin: 0 0 2.5rem 0;
  font-weight: 900;
  color: #0da6f3;
  text-align: center;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
  text-align: center;
}

.contenedor-form {
  background-color: #1a1b15;
  display: flex;
  justify-content: space-between;
  min-height: 100vh;
}
.contenedor-form .imagen {
  height: auto;
  width: 55%;
  background-image: url("../../src/img/1.jpg");
  background-size: cover;
  background-position: center center;
  background-repeat: no-repeat;
}
@media (max-width: 908px) {
  .contenedor-form .imagen {
    min-height: 30vh;
    width: 100%;
  }
}
.contenedor-form .contendor-formulario {
  width: 49%;
  padding: 1rem 0px;
}
@media (max-width: 908px) {
  .contenedor-form .contendor-formulario {
    height: 70%;
    width: 100%;
  }
}
.contenedor-form .seccion {
  display: none;
  margin: 3rem 0px;
}
.contenedor-form .tab-seleccionado {
  display: block !important;
}

@media (max-width: 908px) {
  .contenedor-form {
    flex-direction: column;
  }
}
.tabs {
  display: grid;
  gap: 2rem;
  grid-template-columns: repeat(3, 1fr);
}
.tabs button {
  border: none;
  font-size: 1.9rem;
  padding: 5px;
  text-align: center;
  text-transform: uppercase;
  background-color: white;
}
.tabs button.actual {
  background-color: #0da6f3;
  color: white;
}
.tabs button:hover {
  cursor: pointer;
}

@media (max-width: 908px) {
  .tabs {
    display: grid;
    gap: 1rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
.listado_servicios {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  padding: 0px 5px;
  margin-top: 1rem;
}
.listado_servicios .servicio {
  background-color: #FFFFFF;
  border-radius: 9px;
  margin: 1rem 0px;
  width: 49%;
  padding: 2rem;
  cursor: pointer;
}
.listado_servicios .servicio p {
  font-size: 1.7rem;
  font-weight: bold;
  color: black;
  margin: 0px 0px 1rem 0px;
}
.listado_servicios .servicio span {
  font-weight: bold;
  color: #ff8000;
  font-size: 2rem;
}
@media (max-width: 480px) {
  .listado_servicios .servicio {
    padding: 1rem;
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
  .listado_servicios .servicio p {
    font-size: 1.5rem;
    text-align: center;
    font-weight: lighter;
  }
  .listado_servicios .servicio span {
    font-size: 1.7rem;
  }
}
.listado_servicios .seleccionar {
  background-color: #0da6f3;
}

.paginacion {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.paginacion button {
  border: none;
  background-color: #0da6f3;
  color: white;
  padding: 6px 3rem;
  text-align: center;
  font-size: 2rem;
  cursor: pointer;
}
.paginacion button.ocultar {
  visibility: hidden;
}

.formulario {
  margin-top: 1rem;
}
.formulario .formCampo {
  margin-bottom: 2rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.formulario .formCampo label {
  margin-right: 5px;
  text-transform: uppercase;
  font-weight: 300;
  color: white;
  width: 8rem;
}
.formulario .formCampo input[type=text], .formulario .formCampo input[type=date], .formulario .formCampo input[type=time] {
  width: 90%;
  border: none;
  padding: 1rem 5px;
  border: 2px solid #0da6f3;
  border-radius: 15px;
  background-color: rgba(255, 255, 255, 0);
  color: white;
}
.formulario .alerta {
  font-size: 2rem;
  color: white;
  text-align: center;
  margin-top: 1rem;
  text-transform: uppercase;
}
.formulario .alerta.error {
  color: red;
}

.msjError {
  text-align: center;
  line-height: 27px;
  margin: 1rem 0px;
  text-transform: uppercase;
}